.mx-code-preview-demo {
  position: relative;
  padding: 48px;
  border: 1px solid var(--component-border);
  border-radius: var(--border-radius-medium);
  overflow: auto;
  min-width: 100px;
}
.mx-code-preview-demo-error {
  z-index: 2;
  margin-bottom: 0;
  color: #dc3545;
}
.mx-code-preview-demo-source.error {
  display: none;
}
.mx-code-preview-operations {
  display: flex;
  justify-content: flex-end;
  padding: 12px 0;
}
.mx-code-preview-edit-icon {
  width: 24px;
  height: 24px;
  padding: 0;
  color: var(--text-color-secondary);
  background: var(--bg-color-container);
  border-radius: 100%;
  border: 1px solid var(--component-border);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  width: 28px;
  height: 28px;
}
.mx-code-preview-edit-icon.open {
  color: var(--success-color-active);
}
.mx-code-preview-edit-text {
  color: var(--text-color-secondary);
  padding: 4px 0 4px 12px;
  font-size: 12px;
}
.mx-code-preview-code-container {
  overflow: hidden;
}
.ͼ1 .cm-scroller,
.ͼ3s.cm-editor .cm-scroller {
  line-height: 1.6em;
  font-family: Menlo, Consolas;
}
.cm-gutters {
  opacity: 0.4;
}
.ͼ1.cm-focused {
  outline: none;
}
.cm-gutters {
  opacity: 1;
}
.cm-editor {
  padding: 24px 48px;
}
