.mx-btn-down:active {
  top: 1px;
}
.mx-btn-ripple {
  position: relative;
  overflow: hidden;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.mx-spin {
  display: inline-block;
}
.mx-spin-block {
  display: block;
}
.mx-spin-with-tip {
  text-align: center;
}
.mx-spin-icon {
  color: var(--spin-icon-color);
  font-size: 20px;
}
.mx-spin-tip {
  margin-top: 6px;
  font-size: 14px;
  font-weight: 500;
  color: var(--spin-icon-color);
}
.mx-spin-loading-layer {
  text-align: center;
  -webkit-user-select: none;
          user-select: none;
}
.mx-spin-children {
  position: relative;
}
.mx-spin-children::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: var(--color-spin-layer-bg);
  opacity: 0;
  transition: opacity 0.1s cubic-bezier(0, 0, 1, 1);
  pointer-events: none;
  z-index: 1;
}
.mx-spin-loading {
  position: relative;
  -webkit-user-select: none;
          user-select: none;
}
.mx-spin-loading .mx-spin-loading-layer-inner {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 2;
}
.mx-spin-loading .mx-spin-children::after {
  opacity: 1;
  pointer-events: auto;
}
