.mx-btn-down:active {
  top: 1px;
}
.mx-btn-ripple {
  position: relative;
  overflow: hidden;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.mx-btn {
  display: inline-flex;
  position: relative;
  align-items: center;
  justify-content: center;
  outline: none;
  padding: var(--btn-padding);
  height: var(--btn-height);
  -webkit-appearance: none;
          appearance: none;
  -webkit-user-select: none;
          user-select: none;
  cursor: pointer;
  white-space: nowrap;
  transition: all 0.2s var(--transition-timing-function-standard);
  box-sizing: border-box;
  line-height: 1.5715;
  border-radius: var(--btn-radius);
}
.mx-btn > a:only-child {
  /* stylelint-disable-next-line value-keyword-case */
  color: currentColor;
}
.mx-btn:active {
  transition: none;
}
.mx-btn:active:not(.mx-btn-loading):not(.mx-btn-disabled) {
  transition: none;
}
.mx-btn:empty {
  display: inline-block;
  vertical-align: bottom;
}
.mx-btn-long {
  display: block;
  width: 100%;
}
.mx-btn-loading {
  cursor: default;
  position: relative;
}
.mx-btn-loading::before {
  content: '';
  position: absolute;
  top: -1px;
  right: -1px;
  bottom: -1px;
  left: -1px;
  z-index: 1;
  display: block;
  background: #fff;
  border-radius: inherit;
  opacity: 0.4;
  transition: opacity 1s var(--transition-timing-function-linear);
  pointer-events: none;
}
.mx-btn-loading-fixed-width {
  transition: none;
}
.mx-btn-icon-only {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  vertical-align: top;
  padding: 0;
  width: 32px;
}
.mx-btn > svg + span {
  margin-left: 8px;
}
.mx-btn > span + span {
  margin-left: 8px;
}
