.anchor-list {
  position: relative;
  width: 170px;
  flex-shrink: 0;
  min-height: 100vh;
  box-sizing: border-box;
  background-color: var(--bg-color-container);
  margin-right: 20px;
}
.anchor-list_fixed {
  position: fixed;
  top: 100px;
}
@media screen and (max-width: 950px) {
  .anchor-list {
    display: none;
  }
}
